import { useState, useEffect } from "react";

export interface MobileDetectionResult {
  isMobile: boolean;
  width: number;
  height: number;
  detectionDetails: {
    isMobileWidth: boolean;
    isMobileUA: boolean;
    isTouchDevice: boolean;
    isPortraitOrientation: boolean;
    mediaQueryMatch: boolean;
    isHighDPR: boolean;
    mobileScore: number;
    userAgent: string;
  };
}

/**
 *     
 *
 *            
 * -     ( )
 * -  User-Agent   (  )
 * -    
 * -    
 * - CSS   
 * -   
 */
export const useMobileDetection = (): MobileDetectionResult => {
  const [detection, setDetection] = useState<MobileDetectionResult>({
    isMobile: false,
    width: 0,
    height: 0,
    detectionDetails: {
      isMobileWidth: false,
      isMobileUA: false,
      isTouchDevice: false,
      isPortraitOrientation: false,
      mediaQueryMatch: false,
      isHighDPR: false,
      mobileScore: 0,
      userAgent: "",
    },
  });

  useEffect(() => {
    const detectMobile = () => {
      const width = window.innerWidth;
      const height = window.innerHeight;
      const userAgent = navigator.userAgent.toLowerCase();

      // 1.     ( 1)
      const isMobileWidth = width < 1024; // lg breakpoint

      // 2.  User-Agent  (  )
      const isMobileUA =
        //   
        /android|webos|iphone|ipad|ipod|blackberry|iemobile|opera mini/i.test(
          userAgent,
        ) ||
        //    
        (userAgent.includes("chrome") && userAgent.includes("mobile")) ||
        (userAgent.includes("chrome") && userAgent.includes("android")) ||
        //  
        userAgent.includes("samsungbrowser") ||
        //   
        userAgent.includes("mobile") ||
        userAgent.includes("phone");

      // 3.   
      const isTouchDevice =
        "ontouchstart" in window ||
        navigator.maxTouchPoints > 0 ||
        // @ts-ignore - IE 
        navigator.msMaxTouchPoints > 0;

      // 4.    (    )
      const isPortraitOrientation = height > width;

      // 5. CSS    
      const mediaQueryMatch = window.matchMedia("(max-width: 1023px)").matches;

      // 6.    (  )
      const isHighDPR = window.devicePixelRatio > 1.5;

      // 7. :    
      const isSmallScreen = width < 768 && height < 1024;

      //    (   )
      const mobileScore = [
        isMobileWidth ? 3 : 0, //   ( )
        isMobileUA ? 2 : 0, // User-Agent (  )
        isTouchDevice ? 1.5 : 0, //   ( )
        isPortraitOrientation && width < 800 ? 1 : 0, //   +  
        mediaQueryMatch ? 1 : 0, // CSS  
        isHighDPR ? 0.5 : 0, //  ()
        isSmallScreen ? 1 : 0, //   ( )
      ].reduce((sum, score) => sum + score, 0);

      const finalIsMobile = mobileScore >= 3; //  3

      const detectionDetails = {
        isMobileWidth,
        isMobileUA,
        isTouchDevice,
        isPortraitOrientation,
        mediaQueryMatch,
        isHighDPR,
        mobileScore,
        userAgent: navigator.userAgent,
      };

      //   
      if (process.env.NODE_ENV === "development") {
        console.log("    :", {
          width,
          height,
          finalIsMobile,
          ...detectionDetails,
        });
      }

      setDetection({
        isMobile: finalIsMobile,
        width,
        height,
        detectionDetails,
      });
    };

    //  
    detectMobile();

    //   
    window.addEventListener("resize", detectMobile);
    window.addEventListener("orientationchange", detectMobile);

    return () => {
      window.removeEventListener("resize", detectMobile);
      window.removeEventListener("orientationchange", detectMobile);
    };
  }, []);

  return detection;
};

/**
 *     (boolean )
 */
export const useIsMobile = (): boolean => {
  const { isMobile } = useMobileDetection();
  return isMobile;
};
