import React from "react";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from "react-router-dom";
import { AuthProvider } from "@/contexts/AuthContext";
import { TenantProvider } from "@/contexts/TenantContext";
import { AuthGuard } from "@/components/auth";
import { AppLayout } from "@/components/layout";
import { InstallPrompt } from "@/components/ui/InstallPrompt";
import { DashboardPage } from "@/pages/DashboardPage";
import { PropertiesPageNew } from "@/pages/PropertiesPageNew";
import { TeamPage } from "@/pages/TeamPage";
import { SettingsPage } from "@/pages/SettingsPage";
import { UserGuidePage } from "@/pages/UserGuidePage";
import { InvitationAccept } from "@/components/team/InvitationAccept";
import "@/styles/globals.css";

function App() {
  return (
    <AuthProvider>
      <TenantProvider>
        <Router>
          <Routes>
            {/*     - AuthGuard    */}
            <Route path="/team/invite" element={<InvitationAccept />} />

            {/*    */}
            <Route
              path="/*"
              element={
                <AuthGuard>
                  <AppLayout>
                    <Routes>
                      <Route path="/" element={<DashboardPage />} />
                      <Route
                        path="/properties"
                        element={<PropertiesPageNew />}
                      />
                      <Route path="/team" element={<TeamPage />} />
                      <Route path="/settings" element={<SettingsPage />} />
                      <Route path="/guide" element={<UserGuidePage />} />

                      {/*   */}
                      <Route
                        path="/tenant/:tenantSlug/*"
                        element={<TenantRoutes />}
                      />

                      {/* 404  */}
                      <Route path="*" element={<Navigate to="/" replace />} />
                    </Routes>

                    {/* PWA   */}
                    <InstallPrompt />
                  </AppLayout>
                </AuthGuard>
              }
            />
          </Routes>
        </Router>
      </TenantProvider>
    </AuthProvider>
  );
}

//   
const TenantRoutes: React.FC = () => {
  return (
    <Routes>
      <Route path="/" element={<DashboardPage />} />
      <Route path="/properties" element={<PropertiesPageNew />} />
      <Route path="/team" element={<TeamPage />} />
      <Route path="/settings" element={<SettingsPage />} />
      <Route path="/guide" element={<UserGuidePage />} />
    </Routes>
  );
};

export default App;
